//
//Fonts - used by FireTV
//

//font-size
$font-base-size: 20pt !default;

//font-family
$font-family-light: "Helvetica Neue Light", "HelveticaNeue-Light", Helvetica,
  Arial, sans-serif !default;
$font-family-medium: "Helvetica Neue Medium", "HelveticaNeue-Medium", Helvetica,
  Arial, sans-serif !default;
$font-family-bold: "Helvetica Neue Bold", "HelveticaNeue-Bold", Helvetica, Arial,
  sans-serif !default;

//Colors
//

$color-app-base-bg: #5a656f !default;
$color-app-header-bg: rgba(#000000, 0.1) !default;

$color-overlay-bg: #000000 !default;
$color-overlay-text: #e8e8e8 !default;

$color-app-dialog-container-bg: rgba(0, 0, 0, 0.8);
$color-app-dialog-bg: #242527;
$color-app-dialog-border: #303539;
$color-app-dialog-head-border: #dadada;
$color-app-dialog-btn-text: #dadada;
$color-app-dialog-btn-bg: #393b3d;
$color-dialog-btn-highlight-start: #eca21e;
$color-dialog-btn-highlight-end: #f0bb58;
$color-dialog-entitlement-text: #949393;
$color-dialog-entitlement-price: #464646;
$color-dialog-button-hightlight-text: black;

$color-title-text: #e8e8e8 !default;
$color-meta-primary-text: #e8e8e8 !default;
$color-meta-secondary-text: #b9bbbd !default;
$color-description-text: #e8e8e8 !default;
$color-box-shadow: rgba(0, 0, 0, 0.44) !default;

$color-menu-base-bg: rgba(#000000, 0.9) !default;

$color-menu-item-text: #b9bbbd !default;
$color-menu-item-bg: rgba(#e8e8e8, 0.1) !default;
$color-menu-item-chosen-text: #e8e8e8 !default;
$color-menu-item-chosen-bg: #3f484f !default;
$color-menu-item-highlight-text: #e8e8e8 !default;
$color-menu-item-highlight-bg: #000000 !default;
$color-menu-item-selected-text: #000000 !default;
$color-menu-item-selected-bg: #ffffff !default;

$color-menu-search-text: #e8e8e8 !default;
$color-menu-search-bg: rgba(#e8e8e8, 0.1) !default;

$color-btn-text: #b9bbbd !default;
$color-btn-bg: #394149 !default;
$color-btn-selected-text: #e8e8e8 !default;
$color-btn-selected-bg: #000000 !default;
$color-btn-selected-border: #ffffff !default;

$color-spinner-bg: #394149 !default;

$color-player-controls-bg: rgba(#000000, 0.8) !default;
$color-timeline-border: #25435a !default;
$color-timeline-bg: #707070 !default;
$color-playhead-border: #394149 !default;
$color-playhead-bg: #e8e8e8 !default;

$color-thumbnail-border: #ffffff !default;
$color-time-live-text: #ff704d !default;
$color-time-upcoming-text: #66e066 !default;

//
//Dimensions
//
$size-leftnav-line-height: 2 !default;
$size-leftnav-collapsed-height: 92px !default;
//
//Positions
//
$pos-top-leftnav-content: 148px !default;
$pos-left-content-position: 150px !default;
$pos-right-leftnav-arrow-icon: right 40px !default;

//
//Animations
//
$ani-durationspeed-fast: 0.2s !default;
$ani-durationspeed-med: 0.25s !default;

$ani-duration-leftnav-menu: $ani-durationspeed-med !default;
$ani-duration-leftnav-item: $ani-durationspeed-med !default;
$ani-duration-maincontent-opacity: $ani-durationspeed-fast !default;
$ani-duration-one-D-container: $ani-durationspeed-med !default;
$ani-duration-shoveler-item: $ani-durationspeed-med !default;

$ani-timedelay-shiner: 4.4s !default;
